.content {
	position: relative;
	padding-top: 20px;
	width: 100%;

	&__title {
		overflow: hidden;
		max-height: 76px;
		box-sizing: border-box;
		font-size: 28px;
		line-height: 1;

		@media (min-width: 375px) {
			font-size: 36px;
		}
	}

	&__wrapper {
		display: grid;
		align-items: center;
		grid-template-rows: auto 1fr;
		height: 100%;
	}

	&__row {
		display: grid;
		grid-template-rows: 1fr;

		@media (min-width: 767px) {
			grid-gap: 40px;
			grid-template-columns: repeat(3, 1fr);
			grid-template-rows: none;
		}
	}
}

.content-item {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	text-decoration: none;
	color: $color-black;

	&:not(:last-child) {
		margin-bottom: 25px;

		@media (min-width: 767px) {
			margin-bottom: 0;
		}
	}

	&__ic {
		width: 100px;
		height: 100px;

		svg {
			width: 100%;
			height: 100%;
		}

		&--travel {
			svg {
				fill: rgb(73, 3, 3);
			}
		}

		&--ballon {
			svg {
				fill: rgb(235, 231, 7);
			}
		}

		&--earth {
			svg {
				fill: rgb(15, 107, 84);
			}
		}
	}

	&__text {
		overflow: hidden;
		max-height: 165px;
		font-size: 18px;
	}
}