html {
  min-height: 100%;
  box-sizing: border-box; }

body {
  min-height: 100%;
  font-family: -apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Arial", sans-serif;
  font-size: 16px; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1280px; }

.wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: auto 1fr auto;
  width: 100%;
  min-height: 100vh; }
  @media (min-width: 767px) {
    .wrapper {
      grid-template-rows: auto 1fr auto; } }

.main-header {
  position: relative;
  width: 100%;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
  background-color: #eee; }
  .main-header__wrapper {
    display: grid;
    justify-items: start;
    grid-template-columns: auto 1fr; }
    @media (min-width: 767px) {
      .main-header__wrapper {
        align-items: center;
        grid-template-columns: 150px 1fr;
        grid-template-rows: none; } }
  .main-header__logo {
    display: inline-block;
    padding: 10px 0;
    height: 70px;
    max-width: 50px; }
    .main-header__logo svg {
      max-width: 100%;
      max-height: 100%;
      fill: #000;
      transition: fill 0.2s; }
    .main-header__logo:hover svg, .main-header__logo:focus svg {
      fill: #737373;
      transition: fill 0.2s; }
  .main-header__nav {
    display: flex;
    justify-content: flex-end;
    justify-self: end; }
  .main-header__nav-list {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding-left: 0; }
  .main-header__nav-link {
    position: relative;
    padding: 10px;
    text-decoration: none;
    color: #000;
    transition: color 0.2s; }
    .main-header__nav-link::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 0;
      height: 1px;
      background-color: #000;
      transition: width 0.2s, left 0.2s; }
    .main-header__nav-link:hover, .main-header__nav-link:focus {
      color: #595959;
      transition: color 0.2s; }
      .main-header__nav-link:hover::after, .main-header__nav-link:focus::after {
        left: 10%;
        width: 80%;
        transition: width 0.2s, left 0.2s; }
    @media (min-width: 767px) {
      .main-header__nav-link {
        padding: 10px 20px; } }

.content {
  position: relative;
  padding-top: 20px;
  width: 100%; }
  .content__title {
    overflow: hidden;
    max-height: 76px;
    box-sizing: border-box;
    font-size: 28px;
    line-height: 1; }
    @media (min-width: 375px) {
      .content__title {
        font-size: 36px; } }
  .content__wrapper {
    display: grid;
    align-items: center;
    grid-template-rows: auto 1fr;
    height: 100%; }
  .content__row {
    display: grid;
    grid-template-rows: 1fr; }
    @media (min-width: 767px) {
      .content__row {
        grid-gap: 40px;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: none; } }

.content-item {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  text-decoration: none;
  color: #000; }
  .content-item:not(:last-child) {
    margin-bottom: 25px; }
    @media (min-width: 767px) {
      .content-item:not(:last-child) {
        margin-bottom: 0; } }
  .content-item__ic {
    width: 100px;
    height: 100px; }
    .content-item__ic svg {
      width: 100%;
      height: 100%; }
    .content-item__ic--travel svg {
      fill: #490303; }
    .content-item__ic--ballon svg {
      fill: #ebe707; }
    .content-item__ic--earth svg {
      fill: #0f6b54; }
  .content-item__text {
    overflow: hidden;
    max-height: 165px;
    font-size: 18px; }

.main-footer {
  position: relative;
  padding: 20px;
  width: 100%; }
  .main-footer__copyright {
    margin: 0;
    text-align: center; }
