*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	min-height: 100%;
	box-sizing: border-box;
}

body {
	min-height: 100%;
	font-family: $font-default;
	font-size: 16px;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1280px;
}

.wrapper {
	display: grid;
	grid-auto-flow: column;
	grid-template-rows: auto 1fr auto;
	width: 100%;
	min-height: 100vh;

	@media (min-width: 767px) {
		grid-template-rows: auto 1fr auto;
	}
}