.main-header {
	position: relative;
	width: 100%;
	box-shadow: 0 1px 10px rgba($color-black, 0.1);
	background-color: #eee;

	&__wrapper {
		display: grid;
		justify-items: start;
		grid-template-columns: auto 1fr;

		@media (min-width: 767px) {
			align-items: center;
			grid-template-columns: 150px 1fr;
			grid-template-rows: none;
		}
	}

	&__logo {
		display: inline-block;
		padding: 10px 0;
		height: 70px;
		max-width: 50px;

		svg {
			max-width: 100%;
			max-height: 100%;
			fill: $color-black;
			transition: fill 0.2s;
		}

		&:hover,
		&:focus {
			svg {
				fill: lighten($color-black, 45%);
				transition: fill 0.2s;
			}
		}
	}

	&__nav {
		display: flex;
		justify-content: flex-end;
		justify-self: end;
	}

	&__nav-list {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		list-style: none;
		margin: 0;
		padding-left: 0;
	}

	&__nav-link {
		position: relative;
		padding: 10px;
		text-decoration: none;
		color: $color-black;
		transition: color 0.2s;

		&::after {
			content: "";
			position: absolute;
			left: 50%;
			bottom: 0;
			width: 0;
			height: 1px;
			background-color: $color-black;
			transition: width 0.2s, left 0.2s;
		}

		&:hover,
		&:focus {
			color: lighten($color-black, 35%);
			transition: color 0.2s;

			&::after {
				left: 10%;
				width: 80%;
				transition: width 0.2s, left 0.2s;
			}
		}

		@media (min-width: 767px) {
			padding: 10px 20px;
		}
	}
}